import { Link } from "react-router-dom";
import ChatBot from "react-simple-chatbot";
import { ThemeProvider } from "styled-components";

const theme = {
  background: "#f5f8fb",
  // fontFamily: "Helvetica Neue",
  headerBgColor: "#f57c00",
  headerFontColor: "#fff",
  headerFontSize: "18px",
  botBubbleColor: "#f57c00",
  botFontColor: "#fff",
  userBubbleColor: "#fff",
  userFontColor: "#4a4a4a",
};

const steps = [
  // Welcome
  {
    id: "welcome",
    message: "    !  ?",
    trigger: "options-questions",
    hideInput: true,
  },
  {
    id: "options-questions",
    options: [
      { value: 1, label: " ", trigger: "location" },
      { value: 2, label: " ", trigger: "time" },
      { value: 3, label: "", trigger: "ask-name" },
    ],
    hideInput: true,
  },

  // Church Location
  {
    id: "location",
    message: "   9419 95 St, Edmonton, AB ",
    trigger: "locationlink",
    hideInput: true,
  },
  {
    id: "locationlink",
    component: (
      <a
        style={{ color: "#fff" }}
        href="https://goo.gl/maps/T6qS2MfTkb1miSyUA"
        target="__blank"
      >
        Google Map  
      </a>
    ),
    asMessage: true,
    trigger: "welcome",
    hideInput: true,
  },

  // Service time
  {
    id: "time",
    message: "   2 30 .",
    trigger: "timelink",
    hideInput: true,
  },
  {
    id: "timelink",
    component: (
      <Link style={{ color: "#fff" }} to="/aboutus#aboutus">
            
      </Link>
    ),
    asMessage: true,
    trigger: "welcome",
    hideInput: true,
  },

  // Inquiry
  {
    id: "ask-name",
    message: " ",
    trigger: "answer-name",
    hideInput: true,
  },
  {
    id: "answer-name",
    user: true,
    trigger: "ask-contact-method",
  },
  {
    id: "ask-contact-method",
    message: "   ",
    trigger: "answer-contact-method",
    hideInput: true,
  },
  {
    id: "answer-contact-method",
    options: [
      { value: 1, label: "", trigger: "ask-email" },
      { value: 2, label: "", trigger: "ask-phone" },
    ],
    hideInput: true,
  },
  {
    id: "ask-email",
    message: " ",
    trigger: "answer-email",
    hideInput: true,
  },
  {
    id: "answer-email",
    user: true,
    trigger: "confirm-email",
  },
  {
    id: "confirm-email",
    user: true,
    trigger: "ask-content",
  },
  {
    id: "ask-phone",
    message: " ",
    trigger: "answer-phone",
    hideInput: true,
  },
  {
    id: "answer-phone",
    user: true,
    trigger: "confirm-phone",
  },
  {
    id: "confirm-phone",
    user: true,
    trigger: "ask-content",
  },
  {
    id: "ask-content",
    message: " ",
    trigger: "answer-content",
    hideInput: true,
  },
  {
    id: "answer-content",
    user: true,
    trigger: "ask-submitted",
  },
  {
    id: "ask-submitted",
    message: " .",
    trigger: "welcome",
    hideInput: true,
  },
];

const CustomChatBot = () => {
  return (
    <ThemeProvider theme={theme}>
      <ChatBot
        headerTitle=""
        botAvatar="apple-icon.png"
        floating={true}
        hideUserAvatar={true}
        botDealy={0}
        userDelay={500}
        style={{ textAlign: "left" }}
        //   floatingIcon={<QuestionAnswerOutlinedIcon />}
        steps={steps}
      />
    </ThemeProvider>
  );
};

export default CustomChatBot;
